import React from "react";
import {
	Form,
	TitleForm,
	TitleTextModal,
	FormContainer,
	FormHeader,
	FormHeaderText,
	FormText,
	Img
} from "./styled";
import { Formik, FormikHelpers } from 'formik'
import Input from "./Input";
import Button from "../ui/Button";
import Question from '../../icons/form/question.svg';
import { object, string } from 'yup'
import Spinner from '../Spinner'

const initialValues = {
	name: '',
	email: '',
	tel: ''
}

const telReg = /^(?:\+38)?(?:\(0\d{2}\)[ .-]?[0-9]{3}[ .-]?[0-9]{2}[ .-]?[0-9]{2}|0\d{2}[ .-]?[0-9]{3}[ .-]?[0-9]{2}[ .-]?[0-9]{2}|0\d{2}[0-9]{7})$/

const validationSchema = object({
	name: string()
		.required(`   '`)
		.min(2, "  '")
		.matches(/^\D+$/, '     '),
	email: string()
		.required(`   '`)
		.email("  "),
	tel: string()
		.required(`   '`)
		.matches(telReg, '    ')
})




const ContactForm = ({ formTitle = ' ?', light, setIsFormSubmitted, setIsModalFormOpen }) => {
	const titleLast = formTitle.split(' ').at(-1)
	const titleFirst = `${formTitle.split(' ').slice(0, -1).join(' ')}`
	return (

		<FormContainer light={light}>
			<FormHeader>
				<FormHeaderText light={light}>{titleFirst} <b>{titleLast}</b></FormHeaderText>
				{!light && (<Img src={Question} alt="" />)}
			</FormHeader>
			<FormText light={light}>      ,         ,   .</FormText>
			<Formik
				initialValues={initialValues}
				validationSchema={validationSchema}
				onSubmit={(values, { setSubmitting, resetForm }) => {
					setTimeout(() => {
						console.log(JSON.stringify(values, null, 2));
						setSubmitting(false);

						if (setIsFormSubmitted) {
							setIsFormSubmitted(true);
						}
						if (setIsModalFormOpen) {
							setIsModalFormOpen(true)
						}

						resetForm()
					}, 1000)
				}}
			>
				{({ handleSubmit, isSubmitting }) => (
					<Form autoComplete="off" onSubmit={handleSubmit}>
						<Input
							name='name'
							type="text"
							placeholder="'"
							disabledChars={/\d/gi}
						/>
						<Input
							name='email'
							type="text"
							placeholder="E-mail"
						/>
						<Input
							name='tel'
							type="phone"
							placeholder=""
							disabledChars={/[^\d]/gi}
						/>
						<Button
							type="submit"
							margin="26px 0 0 0"
							padding="21px 0"
							disabled={isSubmitting}
						>
							{isSubmitting ? (<Spinner size={'25px'} color={'#fff'} />) : ''}
						</Button>
					</Form>
				)}
			</Formik>
		</FormContainer>

	)
}

export default ContactForm;

